import React from 'react';

const SearchBox = ({searchField, searchChange}) => {
	return (
		<div className="pa2">
			<input
				className="tc pa2 br3 ba b--green bg-lightest-blue"
				type="search"
				placeholder="search robots"
				onChange={searchChange}
			/>
		</div>
	);
}

export default SearchBox;